export default {
	data() {
		return {
			searchText: '',
			list: [],
		}
	},
	onLoad() {
		
	},
	onShow() {
		console.log("刷新")
		this.getCateList()
	},
	methods: {
		getCateList() {
			this.list = []
			this.$http.get({
				url: '/v1/rest/club/clubMembersInfo',
				data: {
					clubId: 3
				}
			}).then(resp => {
				console.log(resp)
				if(resp.status == 200) {
					// this.list1 = resp.data.clubMembersA
					this.list = this.list.concat(resp.data.clubMembersA).concat(resp.data.clubMembersE).concat(resp.data.clubMembersD).concat(resp.data.clubMembersC).concat(resp.data.clubMembersB)
				}
			})
		},
		onChange(v) {
			this.list = []
			this.searchText = v.detail
			if(this.searchText !== '') {
				this.$http.get({
					url: '/v1/rest/club/clubMembersInfokeyWord',
					data: {
						clubId: 3,
						keyWord: this.searchText
					}
				}).then(resp => {
					console.log(resp)
					if(resp.status == 200) {
						this.list = resp.data
					}
				})
			}else{
				this.getCateList()
			}
		},
		changeMoney(item) {
			uni.navigateTo({
				url: '/pages/groupOwnerManage/costAdjust/costAdjust?item=' + encodeURIComponent(JSON.stringify(item))
			})
		},
		moneyDetail(item) {
			uni.navigateTo({
				url: '/pages/groupOwnerManage/membershipDetails/membershipDetails?clubId=' + item.clubId + '&userId=' + item.userId
			})
		}
	}
}
